$메인컬러 : #ff0000;
@mixin alert {
    background-color: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}
.red {
    color: $메인컬러;
}
.my-alert {
    @include alert()
}
.my-alert2 {
    @extend .my-alert;
    background: #ffe591;
}
.my-alert p {
    margin-bottom: 0;
}
.none {
    display: none;
}